/* 列表页 */
.list-page{padding-bottom: 250px;}
.list-page .no_page_data{color: #999999;font-size: 24px;padding-top: 140px;text-align: center;}


.list-page .p1th{padding: 45px 0 40px 0;border-bottom: 1px solid #e6e6e6;}
.list-page .p1th .title{font-size: 28px;}
.list-page .p2th{}
.list-page .p2th .wbox{border-bottom: 1px solid #e6e6e6;padding: 22px 0;cursor: pointer;}
.list-page .p2th .wbox:hover{background-color: #fafafa;}
.list-page .p2th .lbox{width: 60px;text-align: center;margin-top: 5px;color: #666666;}
.list-page .p2th .lbox .icon-new{background: url(../../image/task/icon-list-new-26x26.png?__sprite);width: 26px;height: 26px;margin: 0 auto;}
.list-page .p2th .lbox .icon-has{background: url(../../image/task/icon-list-has-26x26.png?__sprite);width: 26px;height: 26px;margin: 0 auto;}
.list-page .p2th .lbox .txt{padding-top: 3px;}
.list-page .p2th .cbox{padding: 0px 0 0 20px;width: 680px;line-height: 20px;}
.list-page .p2th .cbox h3{font-size: 18px;}
.list-page .p2th .cbox .p1{color: #666666;font-size: 14px;padding: 3px 0;}
.list-page .p2th .cbox .p2{color: #aaaaaa;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden; 
}

    
.list-page .p2th .wbox:hover .cbox .p2{
    white-space:  normal;
    text-overflow: none ;
    overflow:  none;
}


.list-page .p2th .rbox{padding: 0px 30px 0 0px;text-align: right;width: 100px;}
.list-page .p2th .rbox .time{padding: 10px 0 10px 0;color: #666666;}
.list-page .p2th .rbox .btn{cursor: pointer;color: #333333;}